.saved-filter-options-container{
    margin-right: 5px;
    margin-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.submit-btn-container{
    margin-left:auto;
}

.submit-btn-container .btn-one{
    border: solid 1px black;
    border-radius:4px;
    margin-right: 5px;
    font-size:10px;
}

.submit-btn-container .btn-two {
    border: solid 1px red;
    border-radius:4px;
    margin-right: 5px;
    font-size:10px;
}

.submit-btn-container .btn-three{
    border: solid 1px rgb(22, 22, 140);
    border-radius:4px;
    margin-right: 5px;
    font-size:10px;
}

.submit-btn-container .btn-four{
    /* border: solid 1px rgb(22, 22, 140); */
    border-radius:4px;
    font-size:10px;
}

.options-to-submit-container{
    list-style-type: none;
    display: flex;
    flex-direction: row;
    width: calc(100% - 10px);
    overflow: auto;
    height: 70px;
    padding: 15px;
    font-family: Helvetica;
}

.options-to-submit{
    background-color:#d1cfcf;
    height: 20px;
    color: white;
    white-space: nowrap;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 5.5px;
    margin-right: 10px;
    padding:15px;
    cursor: pointer;
}

.user-inputs-data-container{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-right: 5px;
    padding: 3px;
}


.active-select{
    background-color: black;
}

.option-category{
    margin-right: 5px;
    margin-left: 4px; 
}

.option-user-select{
    margin-right: 5px;
    margin-left: 4px; 
}

.active-select > div > .option-condition{
    color:#315aaa;
}

/* .secondary.active-select{
    background-color: rgb(236, 204, 163);
} */

.option-condition{
    color:#ffa9a9; 
    margin-right: 5px;
    margin-left: 4px;
}

.option-btn-to-delete{
    width: 20px;
    margin-left: auto;
    cursor: pointer;
}

.legend-for-options {
    display: flex;
    align-items: center;
    margin-right:auto;
   
}

.legend-item-container{
    display:flex;
    align-items: center;
    margin-right:8px;
    padding:10px;
    cursor: pointer;
}

.legend-item-container:hover{
    background-color: #d1cfcf;
    border-radius: 7px;
}


::-webkit-scrollbar {
    width: 0.7em;
    height: 0.7em;
}
  
::-webkit-scrollbar-track {
background: none;
}

::-webkit-scrollbar-thumb {
background: hsl(359 100% 0% /1);
border-radius: 100vw;
}

::-webkit-scrollbar-thumb:hover {
background: hsl(359 100% 0% /4);
}

